 import StrictlyGoods from '@/components/common/StrictlyGoods.vue'
  import AdvertisingPosition from '@/components/common/AdvertisingPosition.vue'
  import Dialog from '@/components/common/Dialog.vue'
export default {
	data() {
		return {
			orderSn:'',
			payPrice:'',
			title:'请确认微信支付是否完成',
			cancelText:'遇到问题',
			confirmText:'已完成',
			isShow: true
		};
	},
	components:{
		StrictlyGoods,
		AdvertisingPosition,
		Dialog
	},
	onLoad(options) {
		this.orderSn = options.orderId
		this.payPrice = options.payPrice
	},
	onShow() {
		
		
	},
	methods: {
		doConfirm(){
			this.isShow = false
			
		},
		doCancel(){
			uni.navigateTo({
				url:'/pages/user/order/detail?orderId='+this.orderSn
			})
		},
	      // 广告位url跳转
	      goPath(url) {
	        if (url) {
	          location.href = url
	        }
	      },
	      // 查看订单详情
	      checkOrderDetal() {
	        // 详情需要 orderId, shopId
			uni.navigateTo({
				url:'/pages/user/order/detail?orderId='+this.orderSn
			})
	      },
	      // 返回首页
	      goHome() {
			  uni.switchTab({
			  	url:'/pages/main/main'
			  })
	      }
	    }
}
